import React, { useEffect } from 'react';
import '../App.css';
import Grid from '@material-ui/core/Grid';
import { BaseScreen } from "../components";
import { Zoom, Fade } from 'react-reveal';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';


export default ({ history }) => {
    const classes = useStyles();
    let { fold } = history.location.state;

    useEffect(_ => {
        if (!history.location.state)
            history.replace("./Portifolio")
        document.title = fold
    }, [])

    return (

        <BaseScreen history={history}>
            <Grid container className='fundoPortifolio'>

                <Grid className='backportifolio' item sm={12} xs={12}>
                    <Fade up>
                        <h1 className='nomeC'>{history.location.state.fold}</h1>
                        <Zoom >
                            <Typography className='nomeC'>
                                {history.location.state.description}
                            </Typography>
                        </Zoom>
                    </Fade>
                </Grid>

                {
                    require(`../assets/${fold}/projects.json`).map(({ id, title, description, showimage, image, images }) => {
                        return <Fade>
                            <Card key={id} className={classes.card}>
                                <CardActionArea
                                    onClick={() => fold && history.push(`/Portifolio/${fold}/${id}`, { title, description, image, images, fold })} >
                                    <CardMedia
                                        component="img"
                                        alt={title}
                                        height="140"
                                        image={require(`../assets/${fold}/${image}/${showimage}`)}
                                        title={title} />
                                    <CardContent>
                                        <Fade right>
                                            <Typography gutterBottom variant="h5" component="h2">
                                                {title}
                                            </Typography>
                                        </Fade>
                                        <Fade left>
                                            <Typography variant="body1" color="textPrimary" component="p">
                                                {description}
                                            </Typography>
                                        </Fade>
                                    </CardContent>
                                </CardActionArea>
                            </Card>
                        </Fade>
                    })
                }

            </Grid>
        </BaseScreen>
    )
}


const useStyles = makeStyles({
    card: {
        '@media (min-width:780px)': {
            maxWidth: 340,
        },
        '@media (max-width:777px)': {
            width: '90%',
        },
        boxShadow: '0 3px 5px 2px rgba(260, 260, 260, .2)',
        backgroundColor: 'lightgray',
        margin: 20,
    },
});