import React, { useEffect } from 'react';
import '../App.css';
import { Grid, Typography } from '@material-ui/core';
import { BaseScreen } from "../components";
import { Zoom, Fade, Bounce } from 'react-reveal';

export default ({ history }) => {

    useEffect(_ => { document.title = "HomePage" }, [])

    return (
        <BaseScreen history={history}>
            <Grid container>

                <Grid style={{
                    backgroundImage: "url(" + require('../assets/background7.jpg') + ")",
                    backgroundPosition: 'center',
                    backgroundSize: 'cover',
                    margin: 0
                }} item sm={12} xs={12}>
                    <Zoom>
                        <h1 className='nomeC'>Lucas Pereira de Souza</h1>
                    </Zoom>
                </Grid>

                {require("../assets/introducao.json").map(({ id, title, description, image }) =>
                    <Grid key={id} style={{
                        backgroundImage: "url(" + require(`../assets/${image}`) + ")",
                        backgroundPosition: 'center',
                        backgroundSize: 'cover',
                        height: 200
                    }} item sm={12} xs={12}>
                        <Fade>
                            <h1 className='nomeC'>{title}</h1>
                            <Bounce>
                                <Typography className='nomeC'>
                                    {description}
                                </Typography>
                            </Bounce>
                        </Fade>
                    </Grid>
                )}

            </Grid>
        </BaseScreen>
    )
}