import React, { useState } from 'react';
import '../App.css';
import Header from "./Header";
import Grid from '@material-ui/core/Grid';
import ChatBot from 'react-simple-chatbot';
import { makeStyles } from '@material-ui/core/styles';

import { ThemeProvider as Theme } from 'styled-components';
import Fab from '@material-ui/core/Fab';
import Message from '@material-ui/icons/Message';
import Close from '@material-ui/icons/Close';


export default ({ history, children }) => {
    let [chat, setChat] = useState(false);
    const classes = useStyles();
    let steps = [
        {
            id: '0',
            message: 'Bem vindo ao chat do meu portifolio',
            trigger: '1',
        },
        {
            id: '1',
            message: 'Onde deseja ir ?',
            trigger: '2',
        },
        {
            id: '2',
            options: [
                { value: 1, label: 'Contato', trigger: () => history.replace('/Contato') },
                { value: 2, label: 'Projetos', trigger: () => history.replace('/Portifolio') },
                { value: 3, label: 'Sobre', trigger: () => history.replace('/About') },
                { value: 4, label: 'Quem  voc?', trigger: 3 },
            ],
        },
        {
            id: '3',
            message: 'Eu sou Lucas Pereira de Souza',
            trigger: '4',
            // user: true
        },
        {
            id: '4',
            message: 'Programador FullStack Web Mobile',
            trigger: '5',
        },
        {
            id: '5',
            message: 'Trabalho com tecnologias como React e Nodejs',
            trigger: '6',
        },
        {
            id: '6',
            message: 'Navegue pelo site e conhea melhor o meu trabalho',
            trigger: '1',
        },


    ];

    return (
        <div className='fundoTelas'>
            <Header history={history} />
            <Grid container className='fundoTelas'  >
                {children}
                <div style={{ width: '100%', position: "fixed", bottom: 0 }}>
                    <div style={{
                        position: 'absolute',
                        bottom: '10px',
                        right: '20px'
                    }}>
                        <Fab onClick={() => { setChat(!chat) }} aria-label="delete" className={classes.fab}>
                            {chat ? <Close color='secondary' /> :
                                <Message color='primary' />}
                        </Fab>
                        {chat &&
                            <Theme theme={themeBot}>
                                <ChatBot
                                    botAvatar={require('../assets/fotoperfil.jpg')}
                                    headerTitle="Chat Bot do Lucas"
                                    speechSynthesis={{ enable: true, lang: 'pt-br' }}
                                    steps={steps}
                                    cache={false}
                                    recognitionEnable={true}
                                    placeholder='Digite o que deseja saber' />
                            </Theme>
                        }
                    </div>
                </div>
            </Grid>

        </div>
    )

}


const themeBot = {
    background: '#000',
    // fontFamily: 'Helvetica Neue',
    headerBgColor: 'red',
    headerFontColor: '#fff',
    headerFontSize: '15px',
    botBubbleColor: 'red',
    botFontColor: '#fff',
    userBubbleColor: '#fff',
    userFontColor: '#000',
};



const useStyles = makeStyles(theme => ({
    fab: {
        margin: theme.spacing(1),
        alignSelf: 'right'
    },
    extendedIcon: {
        marginRight: theme.spacing(1),
    },
}));
