import React, { useEffect } from 'react';
import { Grid, Typography } from '@material-ui/core';
import { BaseScreen } from "../components";
import { Zoom, Fade, Roll } from 'react-reveal';
import '../App.css';

export default ({ history }) => {

    let { title, description, image, fold, images } = history.location.state;

    useEffect(_ => { document.title = title }, [])

    return (
        <BaseScreen history={history}>
            <Grid container>
                <Grid item sm={12} xs={12}>
                    <Fade up>
                        <Roll>
                            <h1 className='nomeC'>{title}</h1>
                        </Roll>
                        <Zoom >
                            <Typography className='nomeC'>
                                {description}
                            </Typography>
                        </Zoom>
                    </Fade>
                </Grid>

                {
                    images.map((img, index) => {
                        return <Grid key={`${index}`} item sm={12} xs={12}>
                            <Fade>
                                <img
                                    alt={img}
                                    style={{ width: '65%', margin: 10 }}
                                    src={require(`../assets/${fold}/${image}/${img}`)} />
                            </Fade>
                        </Grid>
                    })}

            </Grid>
        </BaseScreen>
    )
}

