import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, HashRouter, Route } from "react-router-dom";
import { Home, Contato, Portifolio, Sobre, Produto, Linguagens } from "./screens";
import { ThemeProvider } from '@material-ui/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import './App.css';
import red from '@material-ui/core/colors/red';
import HttpsRedirect from 'react-https-redirect';
import { pedirPermissaoParaReceberNotificacoes } from "./push-notification";

export default _ => {
  let theme = createMuiTheme({
    palette: {
      primary: {
        main: '#000'
      },
      secondary: red,
    },
  });
  useEffect(() => {
    setTimeout(() => pedirPermissaoParaReceberNotificacoes(), 5000)
    //  
  }, [])

  return (
    <HttpsRedirect>

      <div className="App" style={{ backgroundColor: 'black', height: '100%', }} >
        <ThemeProvider theme={theme}>
          <HashRouter>
            <Route exact path="/" component={Home} />
            <Route path="/About" component={Sobre} />
            <Route exact path="/Portifolio" component={Portifolio} />
            <Route exact path="/Portifolio/:linguagens" component={Linguagens} />
            <Route exact path="/Portifolio/:Linguagens/:id" component={Produto} />
            <Route path="/Contato" component={Contato} />
          </HashRouter>
        </ThemeProvider>

      </div >


    </HttpsRedirect>
  );
}
