import React from 'react';
import '../App.css';
import { AppBar, Tabs, Tab } from '@material-ui/core';

export default ({ history }) => {
    return (
        <AppBar className='header' position="static" >
            <Tabs value={window.location.pathname} onChange={(ev, value) => history.push(value)} aria-label="simple tabs example">
                <Tab label="Home" value='/' />
                <Tab label="Sobre" value='/About' />
                <Tab label="Portifolio" value='/Portifolio' />
                <Tab label="Contato" value='/Contato' />
            </Tabs>
        </AppBar>
    )
}