import React, { useEffect } from 'react';
import '../App.css';
import { BaseScreen } from "../components";
import { Grid, Typography } from '@material-ui/core';
import { Zoom, Fade, Bounce } from 'react-reveal';

export default ({ history }) => {

    useEffect(_ => { document.title = "Sobre" }, [])
    return (
        <BaseScreen history={history}>
            <Grid container  >

                {require("../assets/sobre.json").map(({ id, title, description, image }) =>
                    <Grid key={id} style={{
                        backgroundImage: "url(" + require(`../assets/${image}`) + ")",
                        backgroundPosition: 'center',
                        backgroundSize: 'cover',
                        height: 260
                    }} item sm={12} xs={12}>
                        <Fade>
                            <h1 className='nomeC'>{title}</h1>
                            <Bounce>
                                <Typography className='nomeC'>
                                    {description}
                                </Typography>
                            </Bounce>
                        </Fade>
                    </Grid>
                )}

            </Grid>
        </BaseScreen>
    )
}
